import React, { useState } from 'react';
import styles from './homeModal.module.css';
import { authService } from '../../fbase';
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { useEffect } from 'react';

const HomeModal = ({ openModal, setOpenModal }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const onChange = (e) => {
    if (e.target.name === 'email') {
      setEmail(e.target.value);
    } else if (e.target.name === 'password') {
      setPassword(e.target.value);
    }
  };
  useEffect(() => {
    console.log(email);
    console.log(password);
  }, [email, password]);
  const onSubmit = (e) => {
    e.preventDefault();
    createUserWithEmailAndPassword(authService, email, password).then(
      (userId) => {
        const user = userId.user;
      }
    );
  };
  const onClick = async (e) => {
    if (e.target.id === 'modalClose') {
      setOpenModal(!openModal);
    }
  };
  return (
    <div id='modalClose' className={styles.container} onClick={onClick}>
      <div className={styles.contents}>
        <button
          className={styles.closeBtn}
          id='modalClose'
          type='button'
          onClick={onClick}
        >
          X
        </button>
        <p className={styles.title}></p>
        <form className={styles.signForm} onSubmit={onSubmit}>
          <input required type='text' placeholder='' />
          <input
            required
            type='email'
            name='email'
            placeholder='()'
            onChange={onChange}
          />
          <input
            required
            minLength='6'
            maxLength='16'
            type='password'
            name='password'
            placeholder='(6~16 )'
            onChange={onChange}
          />
          <input
            required
            minLength='6'
            maxLength='16'
            type='password'
            placeholder=' '
          />
          <input
            required
            type='tel'
            placeholder=' (-  )'
          />
          <input
            required
            className={styles.signUpBtn}
            type='submit'
            value=''
          />
        </form>
      </div>
    </div>
  );
};

export default HomeModal;
